window.dom = {
    create(string){
        const container = document.createElement("template");
        container.innerHTML = string.trim();
        return container.content.firstChild;
    },
    after(node, node2){
        node.parentNode.insertBefore(node2, node.nextSibling);//ç”¨insertBeforeçš„æ–¹æ³•æŠŠnode2æ’åˆ°nodeçš„ä¸‹ä¸€ä¸ªèŠ‚ç‚¹çš„å‰é¢ï¼Œé‚£ä¹Ÿå°±æ˜¯åœ¨nodeåé¢æ’ä¸ªnode2ã€‚å³ä½¿nodeæ˜¯æœ€åä¸€ä¸ªä¹Ÿå¯ä»¥ã€‚
    },
    before(node,node2){
        node.parentNode.insertBefore(node2, node);
    },
    append(parent,node){
        parent.appendChild(node);
    },
    wrap(node,parent){
        dom.before(node,parent);
        dom.append(parent,node);
    },
    remove(node){
        node.parentNode.removeChild(node);
        return node;//returnä¸€ä¸‹å¯ä»¥ä¿ç•™å¯¹äºè¿™ä¸ªèŠ‚ç‚¹çš„å¼•ç”¨ã€‚
    },
    empty(node){
        const {childNodes} = node;//ç®€å†™ï¼Œç­‰åŒäº const childNodes = node.childNodes
        const array = [];
        let x = node.firstChild;
        while(x){
            array.push(dom.remove(node.firstChild))
            x = node.firstChild
        }
        return array;//åˆ é™¤å¹¶ä¿ç•™å¯¹äºè¿™ä¸ªèŠ‚ç‚¹çš„å¼•ç”¨ã€‚
        //è¿™é‡Œä¸ç”¨forå¾ªç¯éå†æ˜¯å› ä¸ºchildNodesé‡Œé¢çš„å…ƒç´ åˆ æ‰åå®ƒçš„lengthæ˜¯ä¼šå˜åŒ–çš„ï¼Œæœ€ç»ˆä¼šå¤šå‡ºä¸€ä¸ªundefinedã€‚
        //æ³¨æ„è¿™é‡Œç”¨whileå¾ªç¯ä¼šå‡ºç°æ–‡æœ¬èŠ‚ç‚¹ï¼Œå› ä¸ºæœ‰ç©ºæ ¼ï¼ˆå›è½¦ï¼‰ã€‚
    },
    attr(node, name, value){
        if(arguments.length === 3){
            node.setAttribute(name, value)
        }else if(arguments.length === 2){
            return node.getAttribute(name)
        }//è¿™ç§æ–¹æ³•å«â€œé‡è½½â€ï¼›ä¹Ÿå°±æ˜¯æ ¹æ®ä¸åŒçš„å‚æ•°(æˆ–å‚æ•°ä¸åŒçš„é•¿åº¦)è·å–åˆ°ä¸åŒçš„æ•ˆæœ
    },
    text(node, string){
        if(arguments.length === 2){
            if('innerText in node'){
              node.innerText = string//(ie)
            }else{
              node.textContent = string//(firefox / chromeï¼‰
            }//ä»¥ä¸Šçš„å†™ä»£ç æ–¹å¼æˆä¸ºâ€œé€‚é…â€ï¼Œå¯æ»¡è¶³æ‰€æœ‰æµè§ˆå™¨;
            //é™¤äº†å°‘æ•°IEæµè§ˆå™¨åªæ”¯æŒç¬¬ä¸€ç§å†™æ³•å¤–å¤–å…¶ä»–æµè§ˆå™¨éƒ½æ”¯æŒä¸¤ç§å†™æ³•;
            //ä¼šæ”¹å˜å¯¹åº”æ ‡ç­¾å†…æ‰€æœ‰å†…å®¹ï¼Œä¸ä¿ç•™æ ‡ç­¾å†…çš„æ ‡ç­¾
        }else if(arguments.length === 1){
            if('innerText in node'){
              return node.innerText
            }else{
              return node.textContent
            }
        }
    },
    html(node, string){
        if(arguments.length === 2){
          node.innerHTML = string
        }else if(arguments.length === 1){
          return node.innerHTML
        }
    },
    style(node, name, value){
        if(arguments.length === 3){// dom.style(div, 'color', 'red')
            node.style[name] = value
        }else if(arguments.length === 2){// dom.style(div, 'color')
            if(typeof name === 'string'){
                return node.style[name]
            }else if(name instanceof Object){// dom.style(test, {color:'blue'})
                const object = name
                for(let key in object){
                    node.style[key] = object[key]
                }//keyå¯èƒ½æ˜¯borderã€color...æ˜¯ä¸€ä¸ªå˜é‡ï¼Œä¸èƒ½ç”¨node.style.key = ...;å¦‚æœè¿™æ ·å†™keyæ˜¯å­—ç¬¦ä¸²ï¼Œä¸æ˜¯å˜é‡ã€‚
            }
        }
        
    },
    class:{
        add(node,  className){
            node.classList.add(className)
        },
        remove(node, className){
            node.classList.remove(className)
        },
        has(node, className){
            return node.classList.contains(className)
        }
    },
    on(node, eventName, fn){
        node.addEventListener(eventName, fn)
    },
    off(node, eventName, fn){
        node.removeEventListener(eventName, fn)
    },
    find(selector, scope){
        return (scope || document).querySelectorAll(selector)//å¦‚æœæœ‰scopeï¼Œå°±åœ¨scopeé‡Œé¢è°ƒç”¨document.querySelectorAllï¼›æ²¡æœ‰å°±ç”¨documentè°ƒç”¨document.querySelectorAll
    },
    //ç»™ä¸€ä¸ªé€‰æ‹©å™¨ï¼Œè¿”å›å¯¹åº”çš„å…ƒç´ ï¼›æ— è®ºç»™çš„é€‰æ‹©å™¨æœ‰å‡ ä¸ªå…ƒç´ ï¼Œé€šè¿‡è°ƒç”¨document.querySelectorAllå…¨éƒ¨è¿”å›ä¸€ä¸ªæ•°ç»„
    //scope : èŒƒå›´
    parent(node){
        return node.parentNode
    },
    children(node){
        return node.children
    },
    siblings(node){
        return Array.from(node.parentNode.children)
        .filter(n=>n!==node)
    },
    //å°†ä¼ªæ•°ç»„node.parentNode.childrenå˜æˆæ•°ç»„åä½¿ç”¨filterå°†ä¸ç­‰äºnodeçš„æ”¾åˆ°æ•°ç»„é‡Œé¢
    // !== ç¬¦å·æ˜¯ä¸€ä¸ª ï¼å’Œä¸¤ä¸ª = æ‰“å‡ºæ¥çš„
    next(node){
        let x = node.nextSibling
        while(x && x.nodeType === 3){
            x = x.nextSibling
        }
        return x
    },
    //nodeType çš„ text æ˜¯ 3 (å¯æŸ¥MDN)
    //ä»£ç æ„æ€æ˜¯å½“xå­˜åœ¨å¹¶ä¸”æ˜¯æ–‡æœ¬æ—¶è¿”å›æ–‡æœ¬ä¸‹ä¸€ä¸ªèŠ‚ç‚¹ï¼Œç›´åˆ°ä¸æ˜¯æ–‡æœ¬çš„èŠ‚ç‚¹ï¼›æœ€åæ²¡æœ‰äº†ä¹Ÿè¦è¿”å›
    previous(node){
        let x = node.previousSibling
        while(x && x.nodeType === 3){
            x = x.previousSibling
        }
        return x
    },
    each(nodeList, fn){
        for(let i=0;i<nodeList.length;i++){
            fn.call(null,nodeList[i])
        }
    },
    index(node){
        const list = dom.children(node.parentNode)
        let i
        for(i=0;i<list.length;i++){
            if(list[i] === node){
                break
            }
        }
        return i
    }
};
